import React, {Component} from 'react';
import NewsItem from '../components/NewsItem.js'

export class FullCoveragePage extends Component {
    constructor(props){
        super(props);
        this.state= {
            articles:[]
        }

    }
    componentDidMount() {
        let url = ""

        fetch(url, { method: "GET", headers: {
            "Content-Type": "text/plain"}})
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            this.setState({
                articles:data
            })
        })
        .catch((err) => {
            console.log(err.message);
        })
    }
    
    render() {
        return (
            <div className='full-coverage'>
                <div className='container'>
                    <hr className='shortbar'/>
                    <h3 className='intro-title'>This is Full Spectrum, below you will find the same article covered from different sources. We hope to widen your persepctive </h3>
                </div>
                <div className='container'>
                    <img className="img-fluid logo-img" src={this.props.article.url} alt={this.props.source.id}/>               
                </div>
                <div className='container'>
                <hr className='longbar'/>
                    {
                        this.state.articles.map((article, i) => {
                            return (
                                <NewsItem {...article} key={i} category={'headline'}/>
                            )
                        })
                    }
                </div>

            </div>
        )
    }
}

export default FullCoveragePage;
